import React, { useState } from "react";
import {
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CRow,
  CAlert,
} from "@coreui/react";
import Select from "react-select";
import TextField from "@material-ui/core/TextField";
import axios from "axios";

const Badges = () => {
  const [title, setTitile] = useState("");
  const [message, setMessage] = useState("");

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  const sendNotification = () => {
    axios
      .post(
        "https://onesignal.com/api/v1/notifications",
        {
          included_segments: [
            "Active Users",
            "Inactive Users",
            "Subscribed Users",
          ],
          app_id: process.env.REACT_APP_ONESIGNAL_ID,
          channel_for_external_user_ids: "push",
          data: { foo: message },
          contents: { en: title },
          headings: { en: title },
          url: "http://ultimodeal.com.np",
          big_picture:
            "https://pyxis.nymag.com/v1/imgs/ba7/f9a/7e2b4a61b237d833cddd783fe95585bf80-sexy-carrot.rsquare.w700.jpg",
          // buttons: [{ id: "123", text: "first button", icon: "avatars/1.jpg" }],
        },
        {
          headers: {
            Authorization: "Basic " + process.env.REACT_APP_ONESIGNAL_TOKEN,
          },
        }
      )
      .then((res) => {
        setEvent({
          type: "success",
          massage: "Notification has been delevered to all users",
        });
        clearEvent(5000);
        setTitile("");
        setMessage("");
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: "error on Push notification",
        });
        clearEvent(5000);
      });
  };

  return (
    <CRow>
      <CCol xs="13" md="6">
        <p color="secondary">Notifications{""}</p>
        <CCard>
          <CCardHeader className="">
            <h2>Select Device and Push Notification</h2>
          </CCardHeader>
          <CCardBody>
            <div className="attribute-form my-4 ml-12">
              <h3>Select Device</h3>
            </div>
            <Select size="sm" className="mb-2" aria-label="Select Device">
              <option>Select Device to send Notification</option>
              <option>Mobile App</option>
              <option>Gmail</option>
            </Select>
            <div>
              <div className="attribute-form my-3 ml-12">
                <h3>Set Title</h3>
              </div>
              <TextField
                id="outlined-multiline-static"
                label="Your Title"
                value={title}
                variant="outlined"
                onChange={(e) => setTitile(e.target.value)}
              />
            </div>
            <div>
              <div className="attribute-form my-3 ml-12">
                <h3>Set Notification</h3>
              </div>
              <textarea
                cols="50"
                rows="4"
                placeholder="Enter Message"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
              ></textarea>
            </div>
            <br></br>
            <button
              disabled={!title || !message}
              type="button"
              onClick={sendNotification}
              class="btn btn-primary btn-lg"
            >
              Sumbit
            </button>
          </CCardBody>
          <CCardFooter></CCardFooter>
        </CCard>
      </CCol>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </CRow>
  );
};

export default Badges;
